import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import db from 'libs/db';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';

export default function RiskHorizon(props) {

	const [investmentHorizon, setInvestmentHorizon] = React.useState("");
	const STATE = props.location.state

	const [loading, setLoading] = React.useState(true);

	const RadioInputOptions = [
		{ key: '1', name: '1-3 years' },
		{ key: '2', name: '3-5 years' },
		{ key: '3', name: '5+ years' },
	]

	React.useEffect(async () => {
		await db.get(db.model.riskProfile).then(p => {
			if (p) {
				if(p.investmentHorizon){
					setInvestmentHorizon(p.investmentHorizon)
				}
				else {
					setInvestmentHorizon("1")
				}
			} else {
				setInvestmentHorizon("1")
			}
		})
		setLoading(false)
	}, [])

	const onNextPress = () => {
		const horizon = Number(investmentHorizon);
		const risk = Number(STATE.riskScore);
		let id = 0
		if (horizon === 3) {
			id = risk + 10
		} else if (horizon === 2) {
			id = risk + 5
		} else {
			id = risk
		}
		props.history.push({pathname:'/risk-profile/recommended-mintbox',state:{MINTBOXID:id}})
	}

	if (STATE === undefined) {
		props.history.push('/Dashboard')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header="Identifying your investment horizon"
				navigation={props.history}
			/>
			<ProgressBar now={100} className="progress-bar-custom" />
			<div className="dataContainer" style={{ paddingTop: '42px' }}>
				{
					loading ?
					<ActivityLoaderMintbox />
						:
						<>
							<div className="header1dark">Select your investment horizon</div>
							{RadioInputOptions.map((obj) => (
								<div key={obj.key} style={{ display: 'flex', flexDirection: 'row', marginTop: "6%" }}>
									<div className="radio-item">
										<input
											type="radio"
											id={obj.key}
											name="ritem"
											value={obj.key}
											onChange={(val) => {
												setInvestmentHorizon(val.target.value)
											}}
											checked={investmentHorizon === obj.key}
										/>
										<label className="bodyTextDark radio-label" htmlFor={obj.key}>{obj.name}</label>
									</div>
								</div>
							))}
						</>
				}
			</div>
			<div className='footer'>
				<button className='outlineButton'
					onClick={() => props.history.push({pathname:"/risk-profile/1"} )}>
					Start again
				</button>
				<button className='solidButton'
					onClick={() => onNextPress() }>
						Continue
				</button>
			</div>
		</div>
	)
}
